import ThemeToggler from "./ThemeToggler/ThemeToggler";
import Clock from '../Clock/Clock';

function PageFooter({ChangeThemeCallBack}) {
    return (
        <footer>
            <Clock />
            <label>
                <p>Change Theme:</p>
                <ThemeToggler ChangeThemeCallBack={ChangeThemeCallBack} />
            </label>
        </footer>
    );
}

export default PageFooter;