import {Outlet, Link} from 'react-router-dom';
import PageHeader from './PageHeader';
import PageFooter from './PageFooter';

function PageLayout({theme, ChangeThemeCallBack}) {
    return (
        <>
            <div className={theme}>
                <PageHeader />
                <div className='main-content'>
                    <div className='component-selection'>
                        <nav>
                            <ul>
                                <li>
                                    <Link to='/'>Tic Tac Toe</Link>
                                </li>
                                <li>
                                    <Link to='tableExercise'>Table Exercise</Link>    
                                </li>
                                <li>
                                    <Link to='hangman'>Hangman game</Link>
                                </li>
                                <li>
                                    <Link to='numberguess'>Guess the number game</Link>
                                </li>
                            </ul>
                        </nav>
                    </div>
                    <div className='component-display'>
                        <Outlet />
                    </div>
                </div>
                <PageFooter ChangeThemeCallBack={ChangeThemeCallBack} />
            </div>
        </>
    )
}

export default PageLayout;